"use client";
import Image from "next/image";
import Link from "next/link";
import { useState, useEffect } from "react";

export default function Navbar() {
  const [isScrolled, setIsScrolled] = useState(false);
  const [darkMode, setDarkMode] = useState(false);
  const [isMenuOpen, setIsMenuOpen] = useState(false);

  // Toggle Dark Mode
  const toggleDarkMode = () => {
    setDarkMode(!darkMode);
  };

  // Apply Dark/Light Mode Class
  useEffect(() => {
    if (darkMode) {
      document.documentElement.classList.add("dark");
    } else {
      document.documentElement.classList.remove("dark");
    }
  }, [darkMode]);

  // Handle Scroll Event
  useEffect(() => {
    const handleScroll = () => {
      if (window.scrollY > 0) {
        setIsScrolled(true);
      } else {
        setIsScrolled(false);
      }
    };
    window.addEventListener("scroll", handleScroll);
    return () => window.removeEventListener("scroll", handleScroll);
  }, []);

  return (
    <nav
      className={`fixed top-0 left-0 w-full z-50 transition-all duration-300 ${
        isScrolled ? "bg-white shadow-md" : "bg-transparent"
      }`}
    >
      <div className="container mx-auto px-4 py-4 flex items-center justify-around">
        {/* Logo */}
        <div className="flex items-center space-x-2">
          <svg
            xmlns="http://www.w3.org/2000/svg"
            className="h-8 w-8 text-blue-600"
            fill="none"
            viewBox="0 0 24 24"
            stroke="currentColor"
          >
            <path
              strokeLinecap="round"
              strokeLinejoin="round"
              strokeWidth={2}
              d="M5 3v4m0 0v4m0-4h6l-1 4H5m10 0v4m0 0v4m0-4h6l-1-4H15m-7 4h14l2-4H10z"
            />
          </svg>
          <h1 className="text-xl font-bold text-gray-800">AccountTechs</h1>
        </div>
        {/* Navigation Links */}
        <div className="hidden md:flex space-x-6 items-center">
          <a href="/" className="text-gray-600 hover:text-blue-600 transition duration-300">
            Home
          </a>
          <a href="/about" className="text-gray-600 hover:text-blue-600 transition duration-300">
            About
          </a>
          <div className="relative group">
            <button className="text-gray-600 hover:text-blue-600 transition duration-300">
              Services
            </button>
            <div className="absolute hidden group-hover:block bg-white shadow-md p-4 mt-2 rounded-md">
              <a href="/web-development" className="block text-gray-600 hover:text-blue-600 transition duration-300">
                Web Development
              </a>
              <a href="/mobile-apps" className="block text-gray-600 hover:text-blue-600 transition duration-300 mt-2">
                Mobile Apps
              </a>
              <a href="/odoo-erp" className="block text-gray-600 hover:text-blue-600 transition duration-300 mt-2">
                Odoo ERP
              </a>
            </div>
          </div>
          <a href="/products" className="text-gray-600 hover:text-blue-600 transition duration-300">
            Products
          </a>
          <a href="/contact" className="text-gray-600 hover:text-blue-600 transition duration-300">
            Contact
          </a>
        </div>
        {/* Mobile Menu (Hamburger Icon) */}
        <div className="md:hidden">
          <button
            className="text-gray-600 hover:text-blue-600 focus:outline-none"
            onClick={() => setIsMenuOpen(!isMenuOpen)}
          >
            <svg
              xmlns="http://www.w3.org/2000/svg"
              className="h-6 w-6"
              fill="none"
              viewBox="0 0 24 24"
              stroke="currentColor"
            >
              <path
                strokeLinecap="round"
                strokeLinejoin="round"
                strokeWidth={2}
                d="M4 6h16M4 12h16M4 18h16"
              />
            </svg>
          </button>
          {isMenuOpen && (
            <div className="absolute top-16 left-0 w-full bg-white shadow-md p-4">
              <a href="/" className="block text-gray-600 hover:text-blue-600 transition duration-300 mb-2">
                Home
              </a>
              <a href="/about" className="block text-gray-600 hover:text-blue-600 transition duration-300 mb-2">
                About
              </a>
              <a href="/services" className="block text-gray-600 hover:text-blue-600 transition duration-300 mb-2">
                Services
              </a>
              <a href="/products" className="block text-gray-600 hover:text-blue-600 transition duration-300 mb-2">
                Products
              </a>
              <a href="/contact" className="block text-gray-600 hover:text-blue-600 transition duration-300 mb-2">
                Contact
              </a>
            </div>
          )}
        </div>
        {/* Dark Mode Toggle */}
        <div className="ml-4">
          <button
            onClick={toggleDarkMode}
            className="text-gray-600 hover:text-blue-600 focus:outline-none"
          >
            {darkMode ? (
              <svg
                xmlns="http://www.w3.org/2000/svg"
                className="h-6 w-6"
                fill="none"
                viewBox="0 0 24 24"
                stroke="currentColor"
              >
                <path
                  strokeLinecap="round"
                  strokeLinejoin="round"
                  strokeWidth={2}
                  d="M12 3v1m0 16v1m9-9h-1M4 12H3m15.364 6.364l-.707-.707M6.343 6.343l-.707-.707m12.728 0l-.707.707M6.343 17.657l-.707.707M16 12a4 4 0 11-8 0 4 4 0 018 0z"
                />
              </svg>
            ) : (
              <svg
                xmlns="http://www.w3.org/2000/svg"
                className="h-6 w-6"
                fill="none"
                viewBox="0 0 24 24"
                stroke="currentColor"
              >
                <path
                  strokeLinecap="round"
                  strokeLinejoin="round"
                  strokeWidth={2}
                  d="M20.354 15.354A9 9 0 018.646 3.646 9.003 9.003 0 0012 21a9.003 9.003 0 008.354-5.646z"
                />
              </svg>
            )}
          </button>
        </div>
      </div>
    </nav>
    
  );
}